<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>船东中心</BreadcrumbItem>
      <BreadcrumbItem>船东详情</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>船东信息</span>
          <Row slot="content" class="row">
              <Col span="2">姓名</Col>
              <Col span="6">{{result.shipOwnerName ? result.shipOwnerName :'-'}}</Col>
              <Col span="2">手机号</Col>
              <Col span="6">{{result.phone ? result.phone:'-'}}</Col>
              <Col span="2">船务公司</Col>
              <Col span="6">{{result.companyName ? result.companyName :'-'}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">船务公司网址</Col>
              <Col span="8">{{result.companyOfficialUrl ? result.companyOfficialUrl :'-'}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">公司简介</Col>
              <Col span="20">{{result.info ? result.info :'-'}}
              </Col>
          </Row>
          <Row slot="content" class="row">
            <Divider/>
          </Row>
           <Row slot="content" class="row">
             <h3>历史交易记录</h3>
          </Row>
            <Row slot="content"  >
             <table class="table table-row">
                 <tr>
                     <td>#</td>
                     <td>装货港</td>
                     <td>卸货港</td>
                     <td>货方运价</td>
                     <td>装货吨位</td>
                     <td>货种</td>
                     <td>订单创建时间</td>
                     <td>订单结束时间</td>
                 </tr>
                   <tr v-for="(item,index) in result.historyOrder" :key='item.id'>
                     <td>{{index+1}}</td>
                     <td>{{item.loadPorts}}</td>
                     <td>{{item.discPorts}}</td>
                     <td>{{item.freights}}</td>
                     <td>{{item.cargoVolume}}</td>
                     <td>{{item.cargo}}</td>
                     <td>{{item.createTime}}</td>
                     <td>{{item.financeEndTime}}</td>
                 </tr>
             </table>
          </Row>
          <Row slot="content" class="row">
            <Divider/>
          </Row>
           <Row slot="content" class="row">
             <h3>所有船舶信息</h3>
          </Row>
            <Row slot="content"  >
             <table class="table table-row">
                 <tr>
                     <td>#</td>
                     <td>船名</td>
                     <td>mmsi</td>
                     <td>船长</td>
                     <td>船宽</td>
                     <td>载重吨位</td>
                     <td>证书吨位</td>
                     <td>TPC</td>
                     <td>船舶详情</td>
                 </tr>
                   <tr v-for="(item,index) in result.allShipInfo" :key='item.id'>
                     <td>{{index+1}}</td>
                     <td>{{item.shipnameCn}}</td>
                     <td>{{item.mmsi}}</td>
                     <td>{{item.length}}</td>
                     <td>{{item.breadth}}</td>
                     <td>{{item.loadTons}}</td>
                     <td>{{item.certTons}}</td>
                     <td>{{item.tpc}}</td>
                     <td>
                       <router-link :to='{name:"shipdetail",query:{
                       code:item.code
                     }}' target='_blank' >查看</router-link> </td>
                 </tr>
             </table>
          </Row>
           <Row slot="content" class="row">
              <Button type="primary">
                <router-link :to="{
                 path: '/cdCenter/updatecd',query:{
                   code:this.$route.query.code
                }
              }" style="color:#fff">修改船东信息</router-link>
                </Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:{}
            }
        },
        methods:{
          async  cdDetail(){
              let data  = {
                shipOwnerCode:this.$route.query.code
              }
             try{
                   let res = await this.$http.cdDetail(data)
                   if(0==res.data.status && res.data.result instanceof Object ){
                     this.result = res.data.result
                   }
              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
           }
        },
        created(){
          this.cdDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
}
.table, tr ,td{
  border: 1px solid #e4e4ee;
  border-collapse: collapse;
  padding: 8px 30px;
  text-align: center;
}
</style>



